import React from 'react';
import './Nav.css'
import { Navbar, Nav } from 'react-bootstrap';
import logo from '../images/logo.jpg'

function NavBar() {
  return (
    <div>
      <Navbar collapseOnSelect expand="lg" className="navBackground">
  <Navbar.Brand href="#home" className="ml-5">
    <img src={logo} alt="logo" className="logo" />
  </Navbar.Brand>
  <Navbar.Toggle aria-controls="responsive-navbar-nav" className="toggle"/>
  <Navbar.Collapse id="responsive-navbar-nav">
    <Nav className="m-auto">
     
    </Nav>
    <Nav className="mr-5 navitems">
      <Nav.Link href="#" eventKey={1} className="black ft">
       <a href="#"> Home </a>
        </Nav.Link>
      <Nav.Link eventKey={2} href="#" className="black ft">
       <a href="#"> Accounts </a>
      </Nav.Link>
      <Nav.Link href="#" eventKey={3} className="black ft">
       <a href="#"> Log In </a>
      </Nav.Link>
      <Nav.Link href="#" eventKey={4} className="black ft">
        <a href="#"> Contact Us </a>
        </Nav.Link>
      <button className="getStartedButton">
           Get Started
        </button>
    </Nav>
  </Navbar.Collapse>

</Navbar>
    </div>
  )
}

export default NavBar;